<template>
  <div>
      <el-table
      :data="list"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
    >
      <el-table-column prop="id" label="商品编号" width="80"> </el-table-column>
      <el-table-column prop="goodsname" label="商品名称" width="110"></el-table-column>
      <el-table-column prop="price" label="商品价格" width="110"> </el-table-column>
      <el-table-column prop="market_price" label="市场价格" width="110"> </el-table-column>

      <el-table-column prop="img" label="图片"> 
          <template slot-scope="scope">
          <img :src="$img+scope.row.img" alt="">
           </template>
      </el-table-column>
     
      <el-table-column prop="isnew" label="是否新品" width="110"> 
          <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.isnew == 1">是</el-button>
          <el-button type="danger" v-else>否</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="ishot" label="是否热卖" width="110"> 
           <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.ishot == 1">是</el-button>
          <el-button type="danger" v-else>否</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1">启用</el-button>
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="size"
      :total="count"
      @current-change="cPage"
      v-if="count>0"
    ></el-pagination>
  </div>
</template>

<script>
import {mapActions,mapGetters} from "vuex"
import { successAlert } from '../../util/alert'
import {delGood} from "../../util/request"
export default {
    data(){
        return{
            info:{}
        }
    },
    computed:{
        ...mapGetters({
            list:"goods/list",
            count:"goods/count",
            page:"goods/page",
            size:"goods/size"
        })
    },
    methods:{
        ...mapActions({
            requestList:"goods/goodsActions",
            requestCount:"goods/countActions",
            requestPage:"goods/pageActions",
        }),
        cPage(e){
            this.requestPage(e)
             this.requestList()
        },
        edit(id){
            this.$emit("detail",id)
        },
        del(id){
            this.$confirm("确定要删除吗？","提示",{
                confirmButtonText:"确定",
                cancelButtonText:"取消",
                type: "warning",
            }).then(()=>{
                delGood({id:id}).then(res=>{
                    successAlert(res.data.msg)
                    this.requestList()
                    this.requestCount()
                })
            }).catch(()=>{
                this.$message({
                    type:"info",
                    message:"已取消删除"
                })
            })
            
        }
    },
    mounted(){
        this.requestList()
        this.requestCount()
    }
}
</script>

<style scoped>
img{
    width: 100px;
    height: 100px;
}
</style>


