<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow" @opened="opened">
      <el-form :model="form">
        <el-form-item label="一级分类" :label-width="formLabelWidth">
          <el-select
            v-model="form.first_cateid"
            placeholder="请选择"
            @change="cahgneCate"
          >
            <el-option
              v-for="(item, index) in list"
              :key="index"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="二级分类" :label-width="formLabelWidth">
          <el-select v-model="form.second_cateid" placeholder="请选择">
            <el-option
              v-for="(item, index) in secondCate"
              :key="index"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="商品名称" :label-width="formLabelWidth">
          <el-input v-model="form.goodsname" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="价格" :label-width="formLabelWidth">
          <el-input v-model="form.price" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="市场价格" :label-width="formLabelWidth">
          <el-input v-model="form.market_price" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="图片" :label-width="formLabelWidth">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="cahangeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="商品规格" :label-width="formLabelWidth">
          <el-select
            v-model="form.specsid"
            placeholder="请选择"
            @change="changeSpec"
          >
            <el-option
              v-for="(item, index) in specLict"
              :key="index"
              :label="item.specsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="规格属性" :label-width="formLabelWidth">
          <el-select v-model="form.specsattr" placeholder="请选择">
            <el-option
              v-for="item in secondSpec"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="是否新品" :label-width="formLabelWidth">
          <el-radio v-model="form.isnew" :label="1">是</el-radio>
          <el-radio v-model="form.isnew" :label="2">否</el-radio>
        </el-form-item>

        <el-form-item label="是否热卖" :label-width="formLabelWidth">
          <el-radio v-model="form.ishot" :label="1">是</el-radio>
          <el-radio v-model="form.ishot" :label="2">否</el-radio>
        </el-form-item>

        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>

        <el-form-item label="商品描述" :label-width="formLabelWidth">
          <div id="edit"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="ok"
          >添 加</el-button
        >
        <el-button type="primary" v-else @click="update">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { goodsAdd,oneGood,goodUpdata } from "../../util/request";
import E from "wangeditor";
import { successAlert, warningAlert } from "../../util/alert";
export default {
  props: ["info"],
  data() {
    return {
      formLabelWidth: "120px",
      form: {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: "",
        market_price: "",
        img: "",
        description: "",
        specsid: "",
        specsattr: "",
        isnew: 1,
        ishot: 1,
        status: 1,
      },
      //存放二级菜单
      secondCate: [],
      //让图片在当前页面显示
      imageUrl: "",
      //存放商品规格属性
      secondSpec: [],
    };
  },
  computed: {
    ...mapGetters({
      list: "cate/getCate",
      specLict: "spec/list",
      count: "spec/count",
      size: "spec/size",
    }),
  },
  methods: {
    ...mapActions({
      requestCateList: "cate/cateActions",
      requestSpecList: "spec/specActions",
      requestSize: "spec/sizeActions",
      requestCount: "spec/countActions",
      goodsList:"goods/goodsActions",
      goodsCount:"goods/countActions",
    }),
    cahangeImg(e) {
      //设置文件大小
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("文件过大");
        return;
      }
      //设置文件格式
      var ex = [".jpg", ".png", ".gif"];
      var exname = e.name.slice(e.name.lastIndexOf("."));
      if (!ex.some((item) => item == exname)) {
        warningAlert("上传文件格式不正确");
        return;
      }
      this.imageUrl = URL.createObjectURL(e.raw);
      this.form.img = e.raw;
    },
        opened() {
      document.getElementById("edit").innerHTML = "";
      this.editor = new E("#edit");
      this.editor.create();
    },
    cancel() {
      this.imageUrl = "";
      this.info.isShow = false;
      this.form = {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: "",
        market_price: "",
        img: "",
        description: "",
        specsid: "",
        specsattr: "",
        isnew: 1,
        ishot: 1,
        status: 1,
      };
    },
    ok() {
      this.form.description = this.editor.txt.html();
      goodsAdd(this.form).then(res=>{
       if(res.data.code==200){
          successAlert(res.data.msg)
          this.cancel();
          this.goodsList();
          this.goodsCount()
       }else{
         warningAlert(res.data.msg)
       }
      })
    },
    detail(id){
      oneGood({id:id}).then(res=>{
        this.form=res.data.list
        this.imageUrl=this.$img+this.form.img
        this.form.id=id
        this.editor.txt.html( this.form.description)
      })
    },
    update() {
      goodUpdata(this.form).then(res=>{
        if(res.data.code==200){
          successAlert(res.data.msg)
          this.cancel();
          this.goodsList();
        }else{
          warningAlert(res.data.msg)
        }
      })
    },
    cahgneCate(e) {
      this.secondCate = [];
      this.form.second_cateid = "";
      for (const i in this.list) {
        if (this.list[i].id == e) {
          if (this.list[i].children !== undefined) {
            for (const key in this.list[i].children) {
              this.secondCate.push(this.list[i].children[key]);
            }
          }
        }
      }
    },
    changeSpec(e) {
      this.requestCount();
      this.requestSize(this.count);
      this.secondSpec = [];
      this.form.specsattr = "";
      var b = this.specLict.findIndex((i) => i.id == e);
      if (this.specLict[b].attrs) {
        this.specLict[b].attrs.forEach((item) => {
          this.secondSpec.push(item);
        });
      }
      //  console.log(this.secondSpec);
    },
  },
  mounted() {
    this.requestCateList();
    this.requestSpecList();
  },
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.el-icon-plus {
  border: 1px dashed #d9d9d9;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


